<template>
  <div id="ResourcesView">
    <head-bar title="锁头库"></head-bar>
    <search-bar @searchValue="getSearch" @deleteSearchValue="deleteSearchValue"></search-bar>
    <body-card :color-title="title" :data="locksData" @downLoadClick="downLoadClick" v-if="!searchData"></body-card>
    <body-card title="搜索结果" :data="searchData" v-if="searchData"></body-card>
    <bottom></bottom>
 </div>
</template>

<script>
import axios from 'axios'
import headBar from '@/components/headBar.vue'
import searchBar from '@/components/searchBar.vue'
import bodyCard from '@/components/bodyCard.vue'
import bottom from '@/components/bottom.vue'
const ip = 'http://150.158.149.195:33666/'
const userApi = `${ip}api/user/`
const adminApi = `${ip}api/admin/`
const otherApi = `${ip}api/other/`
export default {
  name: 'ResourcesView',
  components: {
    headBar,
    searchBar,
    bodyCard,
    bottom
  },
  data () {
    return {
      locksData: null,
      locksDataLength: null,
      searchData: null
    }
  },
  mounted () {
    this.require()
  },
  methods: {
    require () {
      axios.get(otherApi + 'allLocks').then(res => {
        res = res.data
        if (res.sta) {
          this.locksData = res.data
          this.locksDataLength = res.data.length
        } else this.$store.commit('tips', res.msg)
      }).catch(err => {
        this.$store.commit('tips', `错误：${err.message}`)
      })
    },
    getSearch (value) { // 获取搜索框内容
      let valueUpperCaseSta = false
      for (let i = 0; i < value.length; i++) {
        if (value[i] === value[i].toUpperCase()) {
          valueUpperCaseSta = true
          break
        }
      }
      let result = this.locksData.filter(item => {
        if (valueUpperCaseSta) return item.name.toUpperCase().includes(value)
        else return item.name.toLowerCase().includes(value)
      })
      if (result.length === 0) {
        result = this.locksData.filter(item => {
          return item.type.includes(value)
        })
        if (result.length === 0) {
          this.searchData = null
          this.$store.commit('tips', '未搜索到相关内容')
        } else return this.searchData = result
      } else return this.searchData = result
    },
    deleteSearchValue () {
      this.searchData = null
    },
    downLoadClick (data) {
      const lockId = data.split('|')[0]
      const content = data.split('|')[1]
      axios.get(otherApi + 'lockFiles', {
        params: {
          id: lockId
        }
      }).then(res => {
        const resStatus = res.status === 200
        res = res.data
        if (res.sta) {
          res.data.content = content
          this.$store.commit('changeActionSheetSta', res.data)
        } else this.$store.commit('tips', res.msg)
        resStatus && this.$store.state.downLoadSta ? this.$store.commit('loading', false) : null
        this.$store.commit('changeDownLoadSta', false)
      }).catch(err => {
        this.$store.commit('tips', `错误：${err.message}`)
      })
    }
  },
  computed: {
    title () {
      return `共有 ${this.locksDataLength} 个锁头`
    }
  }
}
</script>

<style lang="scss" scoped>
*{
  width: 142% !important;
}
#ResourcesView{
  width: 100% !important;
  display: flex;
  flex-direction: column;
  align-items: center;
}
